* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #f3f3f3;
    color: #e7d7d6;
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
}

.header {
    font-size: 2rem;
}

.header,
.form {
    min-height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

form input,
form button {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background: white;
}

.todo-input:focus-visible{
    outline: none;
}

form button {
    color: #1b1b34;
    background: #f7fffe;
    cursor: pointer;
    transition: all 0.3s ease;
}

form button:hover {
    background: #3f3f3f;
    color: white;
}

.todo-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.todo-list {
    min-width: 30%;
    list-style: none;
}

.todo {
    margin: 0.5rem;
    background: white;
    font-size: 1.5rem;
    color: #3f3f3f;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.filter-todo {
    padding: 1rem;
    border: 0;
}

.todo li {
    flex: 1;
}

.trash-btn,
.complete-btn {
    background: #3f3f3f;
    color: white;
    border: none;
    padding: 1rem;
    cursor: pointer;
    font-size: 1rem;
}

.complete-btn {
    background: rgb(11, 212, 162);
}

.todo-item {
    padding: 0 0.5rem;
}

.fa-trash,
.fa-check {
    pointer-events: none;
}

.completed {
    text-decoration: line-through;
    opacity: 0.5;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    background-image: none;
}


.select {
    margin: 1rem;
    position: relative;
    overflow: hidden;
}

select {
    color: #2c2c3b;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
    width: 12rem;
}

.select::after {
    content: "\25BC";
    position: absolute;
    top: 0;
    right: 0;
    padding: 1rem;
    background: #2c2c3b;
    cursor: pointer;
    pointer-events: none;
}
